.main {
  background: url(../img/bg/bg1.png) no-repeat center / cover;
  height: 100vh;
  padding-left: 110px;
  display: flex;
  align-items: center;
  position: relative;
  overflow: hidden;
  &.blured {
    filter: blur(2px);
  }
  &__circle {
    position: absolute;
    z-index: 1;
    left: 10%;
    top: 50%;
    transform: translate(-50%, -50%);
    border-radius: 50%;
    background: rgba(255, 255, 255, 0.5);
    width: 85%;
    height: 123%;
    &::before, &::after {
      content: '';
      display: block;
      position: absolute;
      z-index: 1;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      border-radius: 50%;
      background: rgba(255, 255, 255, 0.5);
      width: 113%;
      height: 113%;
    }
    &::after {
      width: 122%;
      height: 122%;
    }
  }
  &__content {
    position: relative;
    z-index: 2;
    max-width: 1200px;
  }
  &__title {
    font-family: $ff-secondary;
    max-width: 520px;
    font-size: 44px;
    font-weight: 600;
    line-height: 52px;
    text-transform: uppercase;
    margin-bottom: 12px;

  }
  &__subtitle {
    font-size: 15px;
    font-weight: 300;
    line-height: 18px;
    text-transform: uppercase;
    margin-bottom: 60px;
  }
  &__features {
    max-width: 330px;
    margin-bottom: 60px;
  }
  &__feature {
    margin-bottom: 24px;
    display: flex;
    align-items: center;
    &::before {
      content: '';
      display: block;
      margin-right: 8px;
      min-width: 20px;
      min-height: 20px;
      background: no-repeat center / contain;
    }
    &:nth-of-type(1)::before {
      background-image: url(../img/icon/check.svg);
    }
    &:nth-of-type(2) {
      margin-bottom: 16px;
    }
    &:nth-of-type(2)::before {
      background-image: url(../img/icon/wallet.svg);
    }
    &:nth-of-type(3)::before {
      background-image: url(../img/icon/clock.svg);
    }
  }
  &__btn {
    padding: 10px 42px;
    border: none;
    background: #FFC700;
    box-shadow: 0px 4px 20px 0px rgba(255, 122, 0, 0.4);
    border-radius: 50px;
    max-width: 310px;
    width: 100%;
    font-family: $ff-secondary;
    font-weight: 600;
    font-size: 17px;
    line-height: 22px;
    text-align: center;
    text-transform: uppercase;
    cursor: pointer;
    margin-bottom: 15px;
    transition: .15s;
    display: block;
    border: 1px solid transparent;
    &:focus {
      outline: none;
      border: 1px solid rgb(223, 128, 5);
    }
    &:hover {
      box-shadow: 0px 1px 8px 0px rgba(255, 122, 0, 0.3);
    }
    &:active {
      transform: translateY(2px);
      background: #e2b205;
    }
  }
  &__outro {
    font-size: 12px;
  }
}



@media (min-width: 1400px) {
  .main {
    &__content {
      margin: 0 auto;
      width: 100%;
    }
  }
}
@media (min-width: 992px) and (max-width: 1199px) {

}
@media (min-width: 768px) and (max-width: 991px) {

}
@media (max-width: 1100px) {
  .main {
    &__title {
      font-size: 24px;
      line-height: 32px;
      margin-bottom: 12px;
    }
    &__subtitle {
      font-size: 11px;
      line-height: 18px;
      margin-bottom: 50px;
    }
  }
}
@media (max-width: 767px) {
  .main {
    padding: 70px 15px;
    background-position-x: 10%;
    &__content {
      margin: 0 auto;
    }
    &__title {
      text-align: center;

    }
    &__subtitle {
      text-align: center;

    }

    &__features {
      max-width: 260px;
    }
    &__feature {
      font-weight: 300;
      font-size: 11px;
      line-height: 13px;
      text-transform: uppercase;
      &::before {
        min-width: 15px;
        min-height: 15px;
      }
      &:nth-of-type(1) {
        margin-bottom: 19px;
      }
      &:nth-of-type(2) {
        margin-bottom: 13px;
      }
    }
    &__btn {
      font-size: 12px;
      line-height: 18px;
      padding: 10px 55px;
      margin: 0 auto;
      margin-bottom: 8px;
      
    }
    &__outro {
      font-size: 11px;
      color: #ffffff;
      text-align: center;
      & a {
        font-weight: 700;
      }
    }
    &__circle { //FIXME закончить
      // display: none;
      width: 200%;
      // height: 123%;
      top: 0;
      left: 50%;
      transform: translate(-50%, -50%);
      background: rgba(255, 255, 255, 0.6);
      &::before {
        background: rgba(255, 255, 255, 0.6);
      }
      &::after {
        display: none;
      }
    }

  }
}
