@charset "UTF-8";
*, *::before, *::after {
  box-sizing: border-box;
}

ul, ol {
  padding: 0;
}

body, h1, h2, h3, h4, p, ul, ol, li, figure, figcaption, blockquote, dl, dd {
  margin: 0;
}

body {
  min-height: 100vh;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  line-height: normal;
}

ul, ol {
  list-style: none;
}

a {
  text-decoration: inherit;
  color: inherit;
}

a:not([class]) {
  text-decoration-skip-ink: auto;
}

img {
  max-width: 100%;
  display: block;
}

input, button, textarea, select {
  font: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}

@font-face {
  font-family: 'Cormorant';
  font-display: swap;
  src: url("../fonts/Cormorant-Medium.woff2") format("woff2"), url("../fonts/Cormorant-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
}

@font-face {
  font-family: 'Cormorant';
  font-display: swap;
  src: url("../fonts/Cormorant-SemiBold.woff2") format("woff2"), url("../fonts/Cormorant-SemiBold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
}

@font-face {
  font-family: 'Gilroy';
  font-display: swap;
  src: url("../fonts/Gilroy-Light.woff2") format("woff2"), url("../fonts/Gilroy-Light.woff") format("woff");
  font-weight: 300;
  font-style: normal;
}

@font-face {
  font-family: 'Gilroy';
  font-display: swap;
  src: url("../fonts/Gilroy-Regular.woff2") format("woff2"), url("../fonts/Gilroy-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: 'Gilroy';
  font-display: swap;
  src: url("../fonts/Gilroy-Bold.woff2") format("woff2"), url("../fonts/Gilroy-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
}

body {
  font-family: "Gilroy", sans-serif;
  min-height: 100vh;
  font-size: 16px;
}

.logo {
  display: flex;
}

.logo__img {
  max-width: 90px;
}

.logo__text {
  font-size: 10px;
  line-height: 12px;
  max-width: 200px;
  border-left: 1px solid #000000;
  padding-left: 10px;
  margin-left: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
}

@media (max-width: 480px) {
  .logo__img {
    max-width: 60px;
  }
  .logo__text {
    font-size: 7px;
    line-height: 8px;
    max-width: 120px;
    padding-left: 3px;
    margin-left: 4px;
  }
}

.header {
  padding: 8px 64px 8px 32px;
  position: absolute;
  z-index: 5;
  width: 100%;
  left: 0;
  top: 0;
  right: 0;
  background: rgba(255, 255, 255, 0.8);
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.1);
}

.header__inner {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header__contacts {
  display: flex;
  align-items: center;
}

.header__geo {
  max-width: 120px;
  margin-right: 60px;
  font-size: 10px;
  line-height: 12px;
  position: relative;
}

.header__geo::before {
  content: '';
  display: block;
  position: absolute;
  left: -30px;
  top: 0;
  width: 15px;
  height: 17px;
  background: no-repeat center / contain;
  background-image: url(../img/icon/geo.svg);
}

.header__tel {
  font-size: 12px;
  font-weight: 700;
  border-bottom: 2px dotted #000000;
  display: inline-block;
}

.header__tel:hover {
  border-bottom-style: solid;
}

@media (max-width: 767px) {
  .header {
    padding: 8px 15px;
  }
  .header__geo {
    display: none;
  }
  .header__tel {
    font-size: 10px;
  }
}

.main {
  background: url(../img/bg/bg1.png) no-repeat center/cover;
  height: 100vh;
  padding-left: 110px;
  display: flex;
  align-items: center;
  position: relative;
  overflow: hidden;
}

.main.blured {
  filter: blur(2px);
}

.main__circle {
  position: absolute;
  z-index: 1;
  left: 10%;
  top: 50%;
  transform: translate(-50%, -50%);
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.5);
  width: 85%;
  height: 123%;
}

.main__circle::before, .main__circle::after {
  content: '';
  display: block;
  position: absolute;
  z-index: 1;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.5);
  width: 113%;
  height: 113%;
}

.main__circle::after {
  width: 122%;
  height: 122%;
}

.main__content {
  position: relative;
  z-index: 2;
  max-width: 1200px;
}

.main__title {
  font-family: "Cormorant", serif;
  max-width: 520px;
  font-size: 44px;
  font-weight: 600;
  line-height: 52px;
  text-transform: uppercase;
  margin-bottom: 12px;
}

.main__subtitle {
  font-size: 15px;
  font-weight: 300;
  line-height: 18px;
  text-transform: uppercase;
  margin-bottom: 60px;
}

.main__features {
  max-width: 330px;
  margin-bottom: 60px;
}

.main__feature {
  margin-bottom: 24px;
  display: flex;
  align-items: center;
}

.main__feature::before {
  content: '';
  display: block;
  margin-right: 8px;
  min-width: 20px;
  min-height: 20px;
  background: no-repeat center / contain;
}

.main__feature:nth-of-type(1)::before {
  background-image: url(../img/icon/check.svg);
}

.main__feature:nth-of-type(2) {
  margin-bottom: 16px;
}

.main__feature:nth-of-type(2)::before {
  background-image: url(../img/icon/wallet.svg);
}

.main__feature:nth-of-type(3)::before {
  background-image: url(../img/icon/clock.svg);
}

.main__btn {
  padding: 10px 42px;
  border: none;
  background: #FFC700;
  box-shadow: 0px 4px 20px 0px rgba(255, 122, 0, 0.4);
  border-radius: 50px;
  max-width: 310px;
  width: 100%;
  font-family: "Cormorant", serif;
  font-weight: 600;
  font-size: 17px;
  line-height: 22px;
  text-align: center;
  text-transform: uppercase;
  cursor: pointer;
  margin-bottom: 15px;
  transition: .15s;
  display: block;
  border: 1px solid transparent;
}

.main__btn:focus {
  outline: none;
  border: 1px solid #df8005;
}

.main__btn:hover {
  box-shadow: 0px 1px 8px 0px rgba(255, 122, 0, 0.3);
}

.main__btn:active {
  transform: translateY(2px);
  background: #e2b205;
}

.main__outro {
  font-size: 12px;
}

@media (min-width: 1400px) {
  .main__content {
    margin: 0 auto;
    width: 100%;
  }
}

@media (max-width: 1100px) {
  .main__title {
    font-size: 24px;
    line-height: 32px;
    margin-bottom: 12px;
  }
  .main__subtitle {
    font-size: 11px;
    line-height: 18px;
    margin-bottom: 50px;
  }
}

@media (max-width: 767px) {
  .main {
    padding: 70px 15px;
    background-position-x: 10%;
  }
  .main__content {
    margin: 0 auto;
  }
  .main__title {
    text-align: center;
  }
  .main__subtitle {
    text-align: center;
  }
  .main__features {
    max-width: 260px;
  }
  .main__feature {
    font-weight: 300;
    font-size: 11px;
    line-height: 13px;
    text-transform: uppercase;
  }
  .main__feature::before {
    min-width: 15px;
    min-height: 15px;
  }
  .main__feature:nth-of-type(1) {
    margin-bottom: 19px;
  }
  .main__feature:nth-of-type(2) {
    margin-bottom: 13px;
  }
  .main__btn {
    font-size: 12px;
    line-height: 18px;
    padding: 10px 55px;
    margin: 0 auto;
    margin-bottom: 8px;
  }
  .main__outro {
    font-size: 11px;
    color: #ffffff;
    text-align: center;
  }
  .main__outro a {
    font-weight: 700;
  }
  .main__circle {
    width: 200%;
    top: 0;
    left: 50%;
    transform: translate(-50%, -50%);
    background: rgba(255, 255, 255, 0.6);
  }
  .main__circle::before {
    background: rgba(255, 255, 255, 0.6);
  }
  .main__circle::after {
    display: none;
  }
}

.quiz {
  background: rgba(175, 177, 185, 0.8);
  position: fixed;
  z-index: 10;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  display: none;
}

.quiz.shown {
  display: flex;
  justify-content: center;
  align-items: center;
}

.quiz__close {
  position: absolute;
  width: 18px;
  height: 18px;
  background: #FFFFFF;
  border-radius: 50%;
  right: 11px;
  top: 8px;
  cursor: pointer;
}

.quiz__close::before, .quiz__close::after {
  content: '';
  display: block;
  border-radius: 2px;
  background: #C4C4C4;
  width: 2px;
  height: 10px;
  position: absolute;
  left: 50%;
  top: 50%;
}

.quiz__close::before {
  transform: translate(-50%, -50%) rotate(45deg);
}

.quiz__close::after {
  transform: translate(-50%, -50%) rotate(-45deg);
}

.quiz__inner {
  background: #FFFFFF;
  border-radius: 20px;
  position: absolute;
  z-index: 2;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  min-height: 200px;
  min-width: 200px;
  display: flex;
  min-height: 580px;
}

.quiz__inner::before, .quiz__inner::after {
  content: '';
  display: block;
  position: absolute;
  z-index: -1;
  top: 16px;
  bottom: 18px;
  left: -18px;
  right: -16px;
  background: rgba(255, 255, 255, 0.6);
  border-radius: 20px;
}

.quiz__inner.autoheight {
  position: relative;
  min-height: auto;
  height: auto;
  bottom: auto;
  transform: none;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: auto;
}

.quiz__main {
  padding: 28px 80px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-width: 600px;
  flex-grow: 1;
}

.quiz__main.hidden {
  display: none;
}

.quiz__sidebar {
  background: #F4F6FA;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  min-width: 225px;
  padding: 18px 16px;
  border-radius: 0 20px 20px 0;
}

.quiz__sidebar.hidden {
  display: none;
}

.quiz__sidebar::before {
  content: '';
  display: block;
  background: #F4F6FA;
  position: absolute;
  left: 0;
  top: 50%;
  transform: translate(-50%, -50%) rotate(45deg);
  height: 37px;
  width: 37px;
}

.quiz__header {
  padding-left: 18px;
  padding-right: 18px;
  margin-bottom: 50px;
}

.quiz__intro {
  font-size: 10px;
  margin-bottom: 18px;
}

.quiz__progress {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.quiz__progress-item {
  background: #E5E5E5;
  border-radius: 2px;
  width: 17%;
  height: 3px;
}

.quiz__progress-item.checked {
  background: #FFC700;
}

.quiz__content-wrapper {
  flex-grow: 1;
  position: relative;
}

.quiz__content.hidden {
  display: none;
  opacity: 0;
}

.quiz__title {
  font-family: "Cormorant", serif;
  font-weight: 600;
  font-size: 17px;
  text-transform: uppercase;
  text-align: center;
  margin-bottom: 20px;
}

.quiz__title.not-allow {
  animation: notAllow linear .35s 2;
}

@keyframes notAllow {
  0% {
    transform: translateX(0px);
  }
  25% {
    transform: translateX(-10px);
  }
  50% {
    transform: translateX(0px);
  }
  75% {
    transform: translateX(10px);
  }
  100% {
    transform: translateX(0px);
  }
}

.quiz__subtitle {
  font-size: 12px;
  text-transform: lowercase;
  text-align: center;
  margin-bottom: 40px;
}

.quiz__footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.quiz__btn {
  font-family: "Cormorant", serif;
  font-weight: 600;
  font-size: 12px;
  text-transform: uppercase;
  padding: 10px 30px 10px 35px;
  border-radius: 50px;
  border: none;
  cursor: pointer;
  display: flex;
  align-items: center;
  border: 1px solid transparent;
}

.quiz__btn:focus {
  outline: none;
  border: 1px solid #df8005;
}

.quiz__btn[disabled] {
  visibility: hidden;
}

.quiz__btn::after {
  content: '';
  display: block;
  background: no-repeat center / contain;
  width: 14px;
  height: 10px;
}

.quiz__btn--prev {
  background: #E5E5E5;
}

.quiz__btn--next {
  background: #FFC700;
}

.quiz__btn--prev::after {
  background-image: url(../img/icon/arrow-left.svg);
  order: -1;
  margin-right: 10px;
}

.quiz__btn--next::after {
  background-image: url(../img/icon/arrow-right.svg);
  margin-left: 10px;
}

.quiz__logo {
  margin-bottom: 15px;
}

.quiz__director-img {
  max-width: 115px;
  width: 100%;
  margin: 0 auto;
  margin-bottom: 10px;
  border-radius: 50%;
  overflow: hidden;
}

.quiz__director-title {
  font-weight: 500;
  font-family: "Cormorant", serif;
  margin-bottom: 5px;
}

.quiz__director-subtitle {
  font-weight: 300;
  font-size: 10px;
  margin-bottom: 25px;
}

.quiz__tooltip {
  padding: 18px 25px;
  font-size: 10px;
  line-height: 12px;
  text-align: center;
  background-color: #ffffff;
  position: relative;
  z-index: 2;
}

.quiz__tooltip::before {
  content: '';
  display: block;
  background: #ffffff;
  position: absolute;
  z-index: -1;
  left: 20px;
  top: 0;
  transform: translateY(-50%) rotate(45deg);
  height: 20px;
  width: 20px;
}

.quiz__tooltip::after {
  content: '';
  display: none;
  position: absolute;
  right: 5px;
  top: 16px;
  width: 10px;
  height: 6px;
  background: no-repeat center / contain;
  background-image: url(../img/icon/dropdown.svg);
}

.quiz__tooltip.closed::after {
  display: block;
}

.quiz__tooltip.opened::after {
  display: block;
  transform: rotate(180deg);
}

.quiz__rooms {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.quiz__rooms-item {
  cursor: pointer;
  flex-basis: calc(50% - 26px);
  border-radius: 5px;
  box-shadow: 0px 4px 10px rgba(27, 82, 121, 0.1);
  margin-bottom: 36px;
  border: 1px solid transparent;
  transition: .15s;
  position: relative;
  background: no-repeat center / cover;
  min-height: 150px;
}

.quiz__rooms-item.checked {
  border: 1px solid #72A4C7;
  box-shadow: 0px 4px 15px rgba(0, 64, 138, 0.2);
}

.quiz__rooms-item:hover {
  border: 1px solid #72A4C7;
  box-shadow: 0px 4px 15px rgba(0, 64, 138, 0.2);
}

.quiz__rooms-img {
  display: none;
}

.quiz__rooms-cb {
  position: relative;
  z-index: -1;
  opacity: 0;
  cursor: pointer;
}

.quiz__rooms-title {
  position: absolute;
  left: 12px;
  bottom: 10px;
  font-size: 12px;
}

.quiz__area {
  margin-top: 100px;
}

.quiz__area-range {
  margin-bottom: 15px;
}

.quiz__area-range .noUi-tooltip::after {
  content: ' м²';
}

.quiz__area-minmax {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 12px;
  margin-bottom: 20px;
}

.quiz__area-min::after, .quiz__area-max::after {
  content: ' м²';
}

.quiz__area-input {
  max-width: 200px;
}

.quiz__area-input > input {
  width: 100%;
  background: #FFFFFF;
  border-radius: 5px;
  text-align: center;
  padding: 10px;
  max-width: none;
  width: 100%;
  color: rgba(0, 0, 0, 0.4);
  transition: 0.15s;
  border: 1px solid rgba(0, 0, 0, 0.4);
}

.quiz__area-input > input:focus {
  color: #000000;
  outline: none;
  box-shadow: 0px 4px 10px rgba(27, 82, 121, 0.1);
  border: 1px solid #72A4C7;
}

.quiz__region-list, .quiz__renovated-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.quiz__region-item, .quiz__renovated-item {
  display: flex;
  align-items: center;
  padding: 12px 10px;
  flex-basis: calc(50% - 16px);
  font-size: 12px;
  cursor: pointer;
  border-radius: 5px;
  box-shadow: 0px 4px 10px rgba(27, 82, 121, 0.1);
  margin-bottom: 18px;
  border: 1px solid transparent;
  transition: .15s;
  position: relative;
  flex-grow: 0;
  flex-shrink: 1;
}

.quiz__region-item.checked, .quiz__renovated-item.checked {
  border: 1px solid #72A4C7;
  box-shadow: 0px 4px 15px rgba(0, 64, 138, 0.2);
}

.quiz__region-item.checked .quiz__region-label::after,
.quiz__region-item.checked .quiz__renovated-label::after, .quiz__renovated-item.checked .quiz__region-label::after,
.quiz__renovated-item.checked .quiz__renovated-label::after {
  display: block;
}

.quiz__region-item:hover, .quiz__renovated-item:hover {
  border: 1px solid #72A4C7;
  box-shadow: 0px 4px 15px rgba(0, 64, 138, 0.2);
}

.quiz__region-radio, .quiz__renovated-radio {
  padding: 0;
  margin: 0;
  position: absolute;
  z-index: -1;
  opacity: 0;
}

.quiz__region-label, .quiz__renovated-label {
  position: relative;
  width: 12px;
  height: 12px;
  margin-right: 10px;
  cursor: pointer;
}

.quiz__region-label::before, .quiz__region-label::after, .quiz__renovated-label::before, .quiz__renovated-label::after {
  cursor: pointer;
  content: '';
  position: absolute;
  display: block;
  border-radius: 3px;
}

.quiz__region-label::before, .quiz__renovated-label::before {
  width: 12px;
  height: 12px;
  background: #E5E5E5;
  left: 0;
  top: 0;
}

.quiz__region-label::after, .quiz__renovated-label::after {
  width: 6px;
  height: 6px;
  background: #999999;
  border-radius: 1.5px;
  left: 3px;
  top: 3px;
  display: none;
}

.quiz__renovated-list {
  flex-direction: column;
  align-items: flex-start;
}

.quiz__renovated-item {
  flex-basis: fill;
  max-width: 140px;
  width: 100%;
}

.quiz__floor {
  margin-top: 100px;
}

.quiz__floor-range {
  margin-bottom: 15px;
}

.quiz__floor-range .noUi-tooltip::after {
  content: ' этаж';
}

.quiz__floor-minmax {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 12px;
  margin-bottom: 20px;
}

.quiz__floor-min::after, .quiz__floor-max::after {
  content: ' этаж';
}

.quiz__floor-input {
  max-width: 200px;
}

.quiz__floor-input > input {
  width: 100%;
  background: #FFFFFF;
  border-radius: 5px;
  text-align: center;
  padding: 10px;
  max-width: none;
  width: 100%;
  color: rgba(0, 0, 0, 0.4);
  transition: 0.15s;
  border: 1px solid rgba(0, 0, 0, 0.4);
}

.quiz__floor-input > input:focus {
  color: #000000;
  outline: none;
  box-shadow: 0px 4px 10px rgba(27, 82, 121, 0.1);
  border: 1px solid #72A4C7;
}

.quiz__confirm {
  display: flex;
  flex-direction: column;
  padding: 22px;
  min-width: 800px;
}

.quiz__confirm.hidden {
  display: none;
}

.quiz__confirm-logo {
  margin-bottom: 60px;
}

.quiz__confirm-logo .logo__img {
  max-width: 160px;
  width: 100%;
  margin: 0 auto;
}

.quiz__confirm-form {
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 255px;
  width: 100%;
  margin: 0 auto;
  margin-bottom: 30px;
}

.quiz__confirm-label {
  font-size: 12px;
  margin-bottom: 5px;
}

.quiz__confirm-input {
  margin-bottom: 54px;
  background: #FFFFFF;
  border-radius: 5px;
  text-align: center;
  padding: 10px;
  max-width: none;
  width: 100%;
  color: rgba(0, 0, 0, 0.4);
  transition: 0.15s;
  border: 1px solid rgba(0, 0, 0, 0.4);
}

.quiz__confirm-input:focus {
  color: #000000;
  outline: none;
  box-shadow: 0px 4px 10px rgba(27, 82, 121, 0.1);
  border: 1px solid #72A4C7;
}

.quiz__confirm-btn {
  padding: 10px 42px;
  border: none;
  background: #FFC700;
  box-shadow: 0px 4px 20px 0px rgba(255, 122, 0, 0.4);
  border-radius: 50px;
  width: 100%;
  font-family: "Cormorant", serif;
  font-weight: 600;
  font-size: 17px;
  line-height: 22px;
  text-align: center;
  text-transform: uppercase;
  cursor: pointer;
  margin-bottom: 15px;
  transition: .15s;
  border: 1px solid transparent;
}

.quiz__confirm-btn:focus {
  outline: none;
  border: 1px solid #df8005;
}

.quiz__confirm-btn:hover {
  box-shadow: 0px 1px 8px 0px rgba(255, 122, 0, 0.3);
}

.quiz__confirm-btn:active {
  transform: translateY(2px);
  background: #e2b205;
}

.quiz__confirm-policy {
  font-size: 8px;
  line-height: 10px;
  text-align: center;
  color: #9C9C9C;
  max-width: 160px;
}

.quiz__success {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px 20px 70px;
}

.quiz__success.hidden {
  display: none;
}

.quiz__success .quiz__title {
  line-height: 30px;
}

.quiz__success-logo {
  margin-bottom: 25px;
}

.quiz__success-logo .logo__img {
  max-width: 115px;
}

.noUi-target {
  height: 5px;
  background-color: #E5E5E5;
}

.noUi-target::before {
  content: '';
  background-color: #FFC700;
  display: block;
  position: absolute;
  width: 12px;
  height: 12px;
  left: -6px;
  top: -4px;
  border-radius: 3px;
}

.noUi-target::after {
  position: absolute;
  left: -6px;
  bottom: -24px;
  font-size: 12px;
  border-radius: 3px;
}

.noUi-connect {
  background-color: #FFC700;
}

.noUi-target {
  border: none;
}

.noUi-handle {
  background-color: #FFC700;
  border-radius: 3px;
  box-shadow: none;
  border: 1px solid transparent;
}

.noUi-handle::before, .noUi-handle::after {
  display: none;
}

.noUi-handle:focus {
  outline: none;
  border: 1px solid #df8005;
}

.noUi-touch-area:focus {
  outline: none;
}

.noUi-tooltip {
  font-size: 12px;
  padding: 7px;
  border: 1px solid #E5E5E5;
  border-radius: 3px;
}

.noUi-horizontal .noUi-handle {
  width: 12px;
  height: 12px;
  right: -6px;
  top: -4px;
  cursor: pointer;
}

@media (max-width: 900px) {
  .quiz__main {
    min-width: 500px;
  }
  .quiz__confirm {
    min-width: 600px;
  }
}

@media (max-width: 767px) {
  .quiz__inner {
    left: 0;
    top: 30px;
    bottom: 30px;
    right: 0;
    width: 100%;
    transform: none;
    display: flex;
    flex-direction: column;
    min-height: 470px;
  }
  .quiz__inner::before, .quiz__inner::after {
    display: none;
  }
  .quiz__main {
    border-radius: 10px;
    padding: 15px;
    min-width: auto;
  }
  .quiz__sidebar {
    order: -1;
    padding: 15px;
    flex-direction: row;
    align-items: flex-start;
    border-radius: 10px 10px 0px 0px;
  }
  .quiz__logo {
    display: none;
  }
  .quiz__director {
    margin-bottom: 0;
    display: flex;
    align-items: flex-start;
  }
  .quiz__director-img {
    max-width: 60px;
    width: 100%;
    border-radius: 25px;
    margin: 0;
  }
  .quiz__director-box {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  .quiz__director-title {
    margin-bottom: 1px;
    font-size: 12px;
  }
  .quiz__director-subtitle {
    margin-bottom: 15px;
    font-size: 8px;
  }
  .quiz__tooltip {
    padding: 4px 15px;
    font-weight: 300;
    text-align: left;
    max-width: 170px;
    width: 100%;
  }
  .quiz__tooltip.opened {
    padding: 0;
    position: absolute;
  }
  .quiz__tooltip.opened > span {
    display: inline-block;
    padding: 4px 15px;
  }
  .quiz__tooltip::before {
    transform: scaleX(1.5) translateY(-30%) rotate(45deg);
    height: 16px;
    width: 16px;
  }
  .quiz__intro {
    display: none;
  }
  .quiz__header {
    margin-bottom: 20px;
  }
  .quiz__title {
    font-size: 15px;
    margin-bottom: 20px;
  }
  .quiz__rooms-item {
    flex-basis: calc(50% - 8px);
    min-height: auto;
    padding-bottom: 20%;
    margin-bottom: 30px;
  }
  .quiz__rooms-title {
    bottom: calc(-6px - 1em);
    left: 0;
  }
  .quiz__btn::after {
    width: 12px;
    height: 7px;
  }
  .quiz__area, .quiz__floor {
    margin-top: 50px;
  }
  .quiz__region-item, .quiz__renovated-item {
    padding: 8px 10px;
    flex-basis: calc(50% - 8px);
    border-radius: 5px;
    margin-bottom: 20px;
    flex-shrink: 0;
    flex-grow: 0;
  }
  .quiz__region-title > span {
    display: none;
  }
  .quiz__footer {
    padding-top: 10px;
  }
  .quiz__subtitle {
    margin-bottom: 28px;
  }
  .quiz__confirm {
    min-width: auto;
  }
  .quiz__confirm-logo {
    margin-bottom: 23px;
  }
  .quiz__confirm-input {
    margin-bottom: 40px;
  }
  .quiz__confirm-btn {
    padding: 13px 20px;
    font-size: 12px;
  }
  .quiz__confirm-policy {
    font-size: 8px;
    line-height: 10px;
    text-align: center;
    color: #9C9C9C;
    max-width: 160px;
  }
  .quiz__success {
    align-items: stretch;
  }
  .quiz__success-logo .logo__img {
    margin-left: auto;
    margin-right: auto;
    max-width: 160px;
    width: 100%;
  }
}

@media (max-height: 750px) and (max-width: 767px) {
  .quiz__inner {
    top: 50%;
    width: 100%;
    min-height: auto;
    height: 485px;
    transform: translateY(-50%);
  }
}

.footer {
  padding: 28px 32px;
  position: absolute;
  z-index: 2;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
}

.footer__inner {
  display: flex;
  justify-content: space-between;
}

.footer__box {
  font-size: 10px;
  color: #E5E5E5;
}

.footer__box:first-of-type {
  color: #9C9C9C;
}

.footer__box > a {
  text-decoration: underline;
}

@media (max-width: 767px) {
  .footer {
    padding: 15px;
  }
  .footer__box {
    font-weight: 300;
    font-size: 7px;
    color: #FFFFFF;
  }
  .footer__box:first-of-type {
    color: #FFFFFF;
  }
}
